<template>
  <div class="zizhushegnbao">
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <div class="main" v-show="!show">
      <div class="title">
        <van-button
          style="background-color: #565353 !important; border: 0"
          @click="goBackSetCookies"
          icon="arrow-left"
          type="primary"
        /><span style="margin: 0 auto">来返义申报</span>
      </div>
      <van-field
        v-model="formValute.real_name"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        placeholder="请输入姓名"
        :rules="[{ required: true, message: '请填写姓名' }]"
        label="姓名："
      />
      <van-field
        label-class="zizhushengbaoLabel"
        v-model="formValute.card_type_text"
        placeholder="(身份证等选择)"
        class="zizhushengbaoFiled"
        @click="showPicker_idCardType = true"
        readonly
        right-icon="arrow"
        label="证件类型："
      >
      </van-field>
      <van-field
        v-model="formValute.id_card"
        class="zizhushengbaoFiled"
        @click="eventIfSelect"
        @blur="getOowndeclare"
        :readonly="!formValute.card_type_text"
        label-class="zizhushengbaoLabel"
        @input="
          () => {
            this.formValute.id_card = this.formValute.id_card.replace(
              /[^\w\.\/]/g,
              ''
            );
          }
        "
        :placeholder="
          formValute.card_type_text === ''
            ? '请先选择证件号类别'
            : '请输入正确的证件号'
        "
        :rules="[{ validator: asyncValidator, message: '请输入正确的证件号' }]"
        label="证件号："
      />
      <van-field
        v-model="formValute.phone"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        clearable
        type="number"
        placeholder="请输入手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
        label="手机号："
      >
      </van-field>
      <van-field
        v-model="formValute.sms_code"
        class="zizhushengbaoFiled"
        :placeholder="
          formValute.phone === '' ? '请先输入手机号' : '请输入验证码'
        "
        :readonly="!formValute.phone"
        label-class="zizhushengbaoLabel"
        center
        clearable
        label="验证码："
      >
        <template #button>
          <van-button
            size="small"
            type="primary"
            native-type="button"
            @click="getsms"
            v-if="!smsIF"
            >获取验证码</van-button
          >
        </template>
      </van-field>
      <van-field
        label-class="zizhushengbaoLabel"
        placeholder="(省、市、县选择)"
        class="zizhushengbaoFiled"
        :rules="[{ required: true, message: '请选择来源地' }]"
        @click="showPicker = true"
        type="textarea"
        rows="1"
        autosize
        v-model="addAdress"
        readonly
        label="来源地："
      />
      <van-field
        label-class="zizhushengbaoLabel"
        placeholder="(选择)"
        class="zizhushengbaoFiled"
        @click="showPicker_juzhudi = true"
        type="textarea"
        rows="1"
        autosize
        v-model="formValute_text.yw_street"
        :rules="[{ required: true, message: '请选择来义后居所' }]"
        readonly
        label="来义后居所："
      />
      <van-field
        v-model="formValute.arrival_time"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        readonly
        :rules="[{ required: true, message: '请选择来义时间' }]"
        @click="showPickerDate = true"
        label="来义时间："
      />
      <van-field
        v-model="formValute.address"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        :rules="[{ required: true, message: '请填写详细地址' }]"
        placeholder="无需输入省市县，须精确到门牌号"
        type="textarea"
        rows="1"
        autosize
        label="详细地址："
      />
      <van-field
        class="zizhushengbaoFiled"
        readonly
        placeholder=" 点击上传行程码"
        @click="showPickerUploader = true"
        label-class="zizhushengbaoLabel"
        input-align="right"
        label="行程码图片："
      >
        <template #input>
          <img
            fit="cover"
            style="width: 5rem; height: 5rem"
            v-if="fileList[0] && fileList[0].status === 'done'"
            :src="fileList[0].content"
          />
        </template>
      </van-field>
      <van-popup v-model="showPicker" round position="bottom">
        <van-cascader
          v-model="cascaderValue"
          title="请选择所在地区"
          :field-names="{
            text: 'name',
            value: 'id',
            children: 'children',
          }"
          :options="columns"
          @change="onChange_zhengjie"
          @close="showPicker = false"
          @finish="onConfirm_zhengjie"
        />
      </van-popup>
      <van-popup v-model="showPicker_juzhudi" round position="bottom">
        <van-picker
          title="来义后居住镇街"
          show-toolbar
          :columns="columnsJuzhudi"
          @confirm="onConfirm_juzhudi"
          @cancel="showPicker_juzhudi = false"
        />
      </van-popup>
      <van-popup v-model="showPickerDate" round position="bottom">
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          title="选择来义时间"
          @confirm="onConfirm_Date"
          @cancel="showPickerDate = false"
        />
      </van-popup>
      <van-popup v-model="showPicker_idCardType" round position="bottom">
        <van-picker
          title="证件类型"
          show-toolbar
          :columns="idCardTypeColums"
          @confirm="onConfirm_idCardType"
          @cancel="onCancel_idCardType"
        />
      </van-popup>
      <div class="dibu">
        <span class="canYouHandBaoGao"> 是否具有48小时核酸报告 </span>
        <van-switch
          v-model="checked"
          active-color="green"
          inactive-color="#dcdee0"
          @input="onInput"
        />
      </div>
      <van-button
        type="primary"
        block
        class="submit"
        @click="onSubmit"
        native-type="submit"
        >提交</van-button
      >

      <!-- </van-form> -->
      <van-dialog
        v-model="showPickerUploader"
        title="点击下面方框选择行程码"
        show-cancel-button
      >
        <van-uploader
          v-model="fileList"
          max-count="1"
          :after-read="afterRead"
        />
      </van-dialog>
    </div>
    <van-overlay :show="overlay">
      <van-loading
        size="24px"
        vertical
        style="
          position: fixed;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        "
        >正在校验姓名和身份证号码</van-loading
      >
    </van-overlay>
    <newsuccess
      :dataList="formValute"
      :dataList_text="formValute_text"
      :addAdress="addAdress"
      :fileList="fileList"
      :id_verify_result="id_verify_result"
      :successShow.sync="successShow"
      :successInfo.sync="successInfo"
      :getRoute="routerName"
      @closeSparingContent="closeSparingContent($event)"
      @Back="back($event)"
      v-if="show"
      class="fixedPOstion"
    ></newsuccess>
    <!-- </van-form> -->
  </div>
</template>

<script>
import cookies from '../lib/util.cookie'
import VDistpicker from 'v-distpicker'
import { areaList } from '@vant/area-data'
import
newsuccess
  from '../components/newsuccess.vue'
import {
  Toast
} from 'vant'
import {
  getAreaUserApi, POSTOwndecomeBackApi, getSmsCode, uploadImg, getOowndeclare
} from '@/common/api/api.js'
import { mixinsChaXun } from '../common/mixinsChaXun'
import {
  goBackAndSetCookies
} from '../common/goBackAndSetCookies'
import Vue from 'vue'
export default {
  components: { VDistpicker, newsuccess },
  mixins: [mixinsChaXun, goBackAndSetCookies],
  data () {
    return {
      routerName: 'goAndBackYiwu',
      successInfo: {},
      successShow: false,
      isHide: false,
      show: false,
      areaList,
      cascaderValue: '',
      juzhudiValue: '',
      columns: [

      ],
      loading: true,
      showDiagler: false,
      smsCode: '',
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(2021, 0, 17),
      idCardTypeColums: [
        {
          value: 'id',
          text: '身份证'
        },
        {
          value: 'passport',
          text: '护照号'
        },
        {
          value: 'officer',
          text: '军官证号'
        }
      ],
      showPickerUploader: false,
      columnsJuzhudi: [],
      showPicker: false,
      showPicker2: false,
      showPickerDate: false,
      showPickerDate_fh: false,
      showPicker_idCardType: false,
      showPicker_juzhudi: false,
      fileList: [],
      formValute: {
        province_id: '',
        city_id: '',
        card_type: 'id',
        real_name: '',
        yw_street_id: '',
        address: '',
        street_id: '',
        arrival_time: '',
        travel_img: '',
        phone: '',
        county_id: '',
        sms_code: '',
        id_card: '',
        has_report: 0,
        card_type_text: '身份证'
      },
      formValute_text: {
        province: '',
        city: '',
        street: '',
        county: ''
      },
      checked: false,
      value: '',
      sms: '',
      smsIF: false,
      setTime: '',
      columns2: [],
      smsdJS: 60,
      newData: [],
      selectuID_FirstList: [],
      selectList_FirstIndex: [],
      selectList_SecondIndex: [],
      levelTwo_BySelect_GetList_Is: [],
      selectListSecond: [],
      acData_second: 0,
      acData: 0,
      isFirst: false,
      msg: 1
    }
  },
  created () {
    // 获取全国4级联动
    this.isFirst = true
    this.getAreaUserApi()
    this.addDate()
    this.getAreaUserApi_yiwu({
      pid: 2832
    })
  },
  computed: {
    addAdress () {
      return this.formValute_text.province + this.formValute_text.city + this.formValute_text.county + this.formValute_text.street
    }
  },
  methods: {
    setSuccessInfoNull () {
      cookies.set('successInfo', null)
      this.successInfo = null
    },
    addDate () {
      const nowDate = new Date()
      const date = {
        year: nowDate.getFullYear(),
        month: nowDate.getMonth() + 1,
        date: nowDate.getDate()
      }
      const newmonth = date.month > 10 ? date.month : '0' + date.month
      const day = date.date >= 10 ? date.date : '0' + date.date
      this.formValute.arrival_time = date.year + '-' + newmonth + '-' + day
      this.currentDate = new Date(this.formValute.arrival_time)
    },
    getOowndeclare () {
      if (this.isFirst) {
        let idCardReg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/// 先用正则判断是否为正确的格式
        if ((idCardReg.test(this.formValute.id_card) && this.formValute.card_type === 'id') || this.formValute.card_type !== 'id') {
          if (idCardReg.test(this.formValute.id_card)) {
            getOowndeclare({
              card_type: this.formValute.card_type,
              real_name: this.formValute.real_name,
              id_card: this.formValute.id_card
            }).then(res => {
              if (res.code === 200) {
                this.isFirst = false
                this.formValute.province_id = ''
                this.formValute.county_id = ''
                this.formValute.city_id = ''
                this.formValute_text.province = ''
                this.formValute_text.county = ''
                this.formValute_text.city = ''
                this.formValute.province_id = res.data.province_id
                this.formValute.county_id = res.data.county_id
                this.formValute.city_id = res.data.city_id
                this.formValute_text.province = res.data.province
                this.formValute_text.county = res.data.county
                this.formValute_text.city = res.data.city
              } else {

              }
            })
          } else {
            if (this.formValute.card_type === 'id') {
              Toast.fail('请填写正确的证件号')
            } else {
              getOowndeclare({
                card_type: this.formValute.card_type,
                real_name: this.formValute.real_name,
                id_card: this.formValute.id_card
              }).then(res => {
                if (res.code === 200) {
                  this.isFirst = false
                  if (res.data) {
                    this.formValute.province_id = ''
                    this.formValute.county_id = ''
                    this.formValute.city_id = ''
                    this.formValute_text.province = ''
                    this.formValute_text.county = ''
                    this.formValute_text.city = ''
                    this.formValute.province_id = res.data.province_id
                    this.formValute.county_id = res.data.county_id
                    this.formValute.city_id = res.data.city_id
                    this.formValute_text.province = res.data.province
                    this.formValute_text.county = res.data.county
                    this.formValute_text.city = res.data.city
                  }
                } else {
                }
              })
            }
          }
        } else {

        }
      }
    },
    getriskdistrictLiandon_ByUid (selectuID, uidText, next) {
      console.log(selectuID)
      getAreaUserApi({
        pid: selectuID
      }).then(res => {
        console.log(res.data, '大师')
        switch (uidText) {
          case 'province_id':
            this.selectList_FirstIndex = this.columns.findIndex(items => items.id === selectuID)
            this.selectuID_FirstList = this.columns[this.selectList_FirstIndex]
            this.selectuID_FirstList.children = res.data
            this.selectuID_FirstList.children.map(item => {
              item.children = []
            })
            this.columns.splice(this.selectList_FirstIndex, 1, this.selectuID_FirstList)
            this.cascaderValue = next
            break
          case 'city_id':
            console.table(this.selectuID_FirstList.children)
            this.selectList_SecondIndex = this.selectuID_FirstList.children.findIndex(items => items.id === selectuID) // 第二级选择的list的index
            this.selectListSecond = this.selectuID_FirstList.children // 第二级的所选择的对象
            // children赋值
            this.selectListSecond[this.selectList_SecondIndex].children = res.data
            // 存储第二级对应的第三级数组
            this.levelTwo_BySelect_GetList_Is = this.selectListSecond[this.selectList_SecondIndex].children
            // 删除对应的数组,重新赋值，以便触发视图更新
            this.columns.splice(this.selectList_FirstIndex, 1, this.selectuID_FirstList)

            this.cascaderValue = next
            if (this.formValute.county_id) {
            }
            break
          // case 'county_id':
          //   let selectList_ThirdIndex = this.levelTwo_BySelect_GetList_Is.findIndex(items => items.id === selectuID)
          //   this.levelTwo_BySelect_GetList_Is[selectList_ThirdIndex].children = res.data.data
          //   console.log('object :>> ', this.levelTwo_BySelect_GetList_Is[selectList_ThirdIndex].children)
          //   this.dataList.splice(this.selectList_FirstIndex, 1, this.selectuID_FirstList)
          //   if (this.formValidate.street_id) {
          //     this.gov_idchecked.push(this.formValidate.street_id)
          //   }
          //   break
          default:
            break
        }
      })
    },
    onFailed (errorInfo) {
      console.log('failed', errorInfo)
    },
    closeSparingContent (show) {
      this.show = show
    },
    imagevalidator () {
      if (this.formValute.image) {
        return true
      } else {
        return false
      }
    },
    onInput (checked) {
      checked ? this.formValute.has_report = 1 : this.formValute.has_report = 0
    },
    getAreaUserApi_yiwu (selectedOptionsObject) {
      getAreaUserApi(selectedOptionsObject).then(res => {
        if (res.code === 200) {
          console.log(res.data)
          res.data.map(item => {
            this.columnsJuzhudi.push({
              value: item.id,
              text: item.name
            })
          })
        }
        console.log(this.columnsJuzhudi)
      })
    },
    onSubmit (values) {
      let formValute = this.formValute
      if (!formValute.real_name) {
        Toast.fail('请输入姓名')
        return false
      }

      var pat = new RegExp('[^a-zA-Z0-9\_\u4e00-\u9fa5]', 'i')
      let realNameChilaReg = /\d+|\w+/
      if (pat.test(formValute.real_name)) {
        console.log(formValute.real_name, pat.test(formValute.real_name))
        Toast.fail('姓名不能包含特殊字符')
        return false
      }
      if (this.formValute.card_type === 'id') {
        if (realNameChilaReg.test(formValute.real_name)) {
          Toast.fail('姓名不能包含字母或数字')
          return false
        }
      }
      if (!formValute.card_type) {
        Toast.fail('请选择证件号类别')
        return false
      }
      if (!formValute.id_card) {
        Toast.fail('请输入证件号')
        return false
      }
      let idCardReg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/// 先用正则判断是否为正确的格式
      if (this.formValute.card_type === 'id') {
        if (!idCardReg.test(formValute.id_card)) {
          Toast.fail('证件号格式错误')
          return false
        } else {
          console.log('校验')
        }
      }
      if (!formValute.phone) {
        Toast.fail('请输入手机号')
        return false
      }
      let myreg = /^1\d{10}$/
      if (!myreg.test(formValute.phone)) {
        Toast.fail('手机号格式错误')
        return false
      }
      if (!formValute.sms_code) {
        Toast.fail('请输入验证码')
        return false
      }
      if (formValute.sms_code.length < 6) {
        Toast.fail('验证码长度不足6位')
        return false
      }
      if (!formValute.province_id) {
        Toast.fail('请选择来源地')
        return false
      }
      if (!formValute.yw_street_id) {
        Toast.fail('请选择来义居住镇街')
        return false
      }
      if (!formValute.address) {
        Toast.fail('请输入详细地址')
        return false
      }
      if (formValute.address.length < 3) {
        Toast.fail('请输入准确详细地址（不小于3个文字）')
        return false
      }
      if (!formValute.arrival_time) {
        Toast.fail('请选择来义时间')
        return false
      }
      if (!this.fileList[0] || this.fileList[0].status !== 'done') {
        Toast.fail('请上传行程码')
        return false
      }
      if (this.formValute.card_type === 'id') {
        this.qgrkk({
          real_name: this.formValute.real_name,
          id_card: this.formValute.id_card
        })
        this.setSuccessInfoNull()
      } else {
        this.id_verify_result = 0
        this.show = true
        this.successShow = false
        this.isHide = true
        this.setSuccessInfoNull()
      }
    },
    onConfirm_idCardType (idCardType) {
      console.log(idCardType, 'SA')
      this.formValute.card_type = idCardType.value
      this.formValute.card_type_text = idCardType.text
      this.showPicker_idCardType = false
    },
    onCancel_idCardType () {

    },
    onConfirm_juzhudi (val) {
      this.formValute.yw_street_id = val.value
      this.formValute_text.yw_street = val.text
      this.showPicker_juzhudi = false
    },
    getAreaUserApi (selectedOptionsObject) {
      getAreaUserApi(selectedOptionsObject).then(res => {
        console.log(res.data)
        this.loading = false
        if (selectedOptionsObject) {
          const _this = this
          switch (selectedOptionsObject.level) {
            case 0:
              this.acData = _this.columns.findIndex(x => x.id === selectedOptionsObject.pid)
              console.log(this.acData)
              this.columns[this.acData].children = res.data
              this.newData = JSON.parse(JSON.stringify(this.columns[this.acData]))
              console.log(this.newData.children, '1级联动')
              this.newData.children.length !== 0
                ? this.newData.children.map(item => {
                  if (item) {
                    item.children = []
                  }
                }) : this.showPicker = false
              this.newData.children.map(item => {
                if (item) {
                  item.children = []
                }
              })
              console.log(this.newData.children.length)
              this.columns.splice(this.acData, 1, this.newData)
              break
            case 1:
              // newData.children = res.data
              console.log(this.newData.children, '2级联动')
              this.acData_second = this.newData.children.findIndex(x => x.id === selectedOptionsObject.pid)
              this.newData.children[this.acData_second].children = res.data
              this.columns.splice(this.acData, 1, this.newData)
              this.newData.children[this.acData_second].children.map(item => {
                item.children = []
              })
              break
            case 2:
              let newDataTwo = this.newData.children[this.acData_second].children
              let acDatathird = newDataTwo.findIndex(x => x.id === selectedOptionsObject.pid)
              if (res.data.length > 0) {
                newDataTwo[acDatathird].children = res.data
              }
              this.columns.splice(this.acData, 1, this.newData)
              break
            default:
              break
          }
          // this.$nextTick(() => {
          //   this.$forceUpdate()
          // })
        } else {
          this.columns = res.data
          this.columns.map(item => {
            if (item.name !== '台湾') {
              item.children = []
            }
          })
        }
      })
    },
    getsms () {
      getSmsCode({
        prefix: 'declare_verify',
        phone: this.formValute.phone
      }).then(res => {
        if (res.code === 200) {
          this.smsIF = true
          this.showDiagler = true
          this.djsSms()
          Toast.success(res.msg)
        } else {
          Toast.fail(res.msg)
        }
      })
    },
    djsSms () {
      this.smsdJS = this.smsdJS - 1
      this.smsIF = true
      if (this.smsdJS <= 0) {
        this.smsIF = false
        this.smsdJS = 60
        clearTimeout(this.setTime)
      } else {
        this.setTime = setTimeout(() => {
          this.djsSms()
        }, 1000)
      }
    },
    afterRead (file) {
      file.status = 'uploading'
      file.message = '上传中...'
      console.log(file)
      uploadImg({
        file: file.content
      }).then(res => {
        if (res.code === 200) {
          console.log(res.data)
          this.formValute.travel_img = res.data.src
          file.status = 'done'
          file.message = '上传成功'
        } else {
          file.status = 'failed'
          file.message = '上传失败'
        }
      }).catch(res => {
        file.status = 'failed'
        file.message = '上传失败'
      })
    },
    onConfirm_zhengjie (zhengjie) {
      console.log(zhengjie, 'zhengjie')
      // this.formValute.zhengjie = zhengjie
      let selectList = zhengjie.selectedOptions
      selectList.map((item, index) => {
        this.formValute.province_id = selectList[0].id
        this.formValute.city_id = selectList[1].id
        this.formValute.county_id = selectList[2].id
        if (selectList[3]) {
          this.formValute.street_id = selectList[3].id
          this.formValute_text.street = selectList[3].name
        } else {
          this.formValute.street_id = ''
          this.formValute_text.street = ''
        }
        this.formValute_text.province = selectList[0].name
        this.formValute_text.city = selectList[1].name
        this.formValute_text.county = selectList[2].name
      })
      this.showPicker = false
    },
    onChange_zhengjie (value) {
      let selectList = value.selectedOptions
      selectList.map((item, index) => {
        if (selectList[0]) {
          this.formValute.province_id = selectList[0].id
          this.formValute_text.province = selectList[0].name
        } else {
          this.formValute.province_id = ''
          this.formValute_text.province = ''
        }
        if (selectList[1]) {
          this.formValute.city_id = selectList[1].id
          this.formValute_text.city = selectList[1].name
        } else {
          this.formValute.city_id = ''
          this.formValute_text.city = ''
        }
        if (selectList[2]) {
          this.formValute.county_id = selectList[2].id
          this.formValute_text.county = selectList[2].name
        } else {
          this.formValute.county_id = ''
          this.formValute_text.county = ''
        }
        if (selectList[3]) {
          this.formValute.street_id = selectList[3].id
          this.formValute_text.street = selectList[3].name
        } else {
          this.formValute.street_id = ''
          this.formValute_text.street = ''
        }
      })
      console.log(value)
      let selectedOptionsObject = value.selectedOptions[value.selectedOptions.length - 1]
      this.getAreaUserApi({
        // eslint-disable-next-line no-use-before-define
        pid: selectedOptionsObject.id,
        level: selectedOptionsObject.level
      })
    },
    onCancel_zhengjie () {
      this.showPicker = false
    },
    choose () {
      this.show = !this.show
    },
    goBack () {
      this.$router.back(-1)
    },
    onChangeProvince (a) {
      console.log(a)
      this.formValute.laiyuandi.province = a
    },
    onConfirm_Date (date) {
      this.showPickerDate = false
      this.formValute.arrival_time = this.$dateTimeFilters(date, 'yyyy-MM-dd')
    },
    asyncValidator (val) {
      if (this.formValute.card_type_text === '身份证') {
        return /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(val)
      } else {
        return true
      }
    },
    eventIfSelect () {
      // 若选择了证件类型
      if (this.formValute.card_type_text) {

      } else {
        Toast.fail('请先选择证件类型')
      }
    },
    back () {
      this.$router.push({
        path: '/'
      })
    }
  }
}
</script>
